body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  text-align: center;
  color: #333333;
}
@font-face {
  font-weight: normal;
  font-family: "PTSans";
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "PTSans";
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.button {
  outline: none;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
ul {
  list-style-type: none;
}
fieldset {
  border: none;
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}
.main-nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 216px;
  font-size: 18px;
  line-height: 18px;
  color: white;
  background-color: #81b3d2;
  margin: 0 auto;
}
.main-nav--closed {
  display: none;
}
.main-nav--opened {
  display: flex;
}
.main-nav__list {
  padding: 0;
  margin: 0;
}
.main-nav__item {
  font-weight: bold;
  padding-top: 16px;
  padding-bottom: 20px;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid gray;
}
.main-nav__link {
  color: #fff;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 54px;
  height: 19px;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  border: none;
  margin-right: 20px;
  margin-top: 14px;
  outline: none;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 32px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav {
    display: none;
  }
}
.header {
